import React, { useState, useRef } from "react";

import emailjs from "@emailjs/browser";

import "./ContactForm.css";

import { FaMapMarkerAlt } from "react-icons/fa";

import { FiPhoneCall } from "react-icons/fi";

import { MdMarkEmailRead } from "react-icons/md";

import { BsPersonCircle } from "react-icons/bs";

import { BiMailSend } from "react-icons/bi";

import { AiTwotonePhone } from "react-icons/ai";

import { GiRotaryPhone } from "react-icons/gi";

export default function ContactForm() {

  const web = useRef(null)
  const socialMedia = useRef(null)
  const graphicDesign = useRef(null)
  const video = useRef(null)

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [contactNumber, setContactNumber] = useState();

  const [serviceOption, setServiceOption] = useState(null);

  const [showMap, setShowMap] = useState(false);

  const handleEnterMap = () => {
    setShowMap(true);
    setShowMail(false);
    setShowPhone(false);
  };

  const [showPhone, setShowPhone] = useState(false);

  const handleEnterPhone = () => {
    setShowMap(false);
    setShowMail(false);
    setShowPhone(true);
  };

  const [showMail, setShowMail] = useState(true);

  const handleEnterEmail = () => {
    setShowMap(false);
    setShowMail(true);
    setShowPhone(false);
  };

  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_3hamhsn",
        "template_10nbb1v",
        form.current,
        "2VHNvy78l20PP-ceW"
      )
      .then(
        (result) => {
          console.log(result.text);
          alert("Message Sent");
        },
        (error) => {
          console.log(error.text);
        }
      );
  };

  return (
    <>
      <div className="contact-form-main-container">
        <h1>Get In Touch!</h1>

        <p>Contact us for a quote</p>

        {/* Options starts from here */}
        <div className="contact-form-options">
          <div
            className="contact-form-options-map"
            onClick={handleEnterMap}
            style={{ backgroundColor: `${showMap ? "#612d91" : "#fff"}` }}
            // onMouseLeave={handleLeaveMap}
          >
            <FaMapMarkerAlt color={showMap ? "#fff" : "#612d91"} size={30} />
          </div>

          <div
            className="contact-form-options-email"
            onClick={handleEnterEmail}
            style={{ backgroundColor: `${showMail ? "#612d91" : "#fff"}` }}
          >
            <MdMarkEmailRead color={showMail ? "#fff" : "#612d91"} size={30} />
          </div>

          <div
            className="contact-form-options-phone"
            onClick={handleEnterPhone}
            style={{ backgroundColor: `${showPhone ? "#612d91" : "#fff"}` }}
            // onMouseLeave={handleLeavePhone}
          >
            <FiPhoneCall color={showPhone ? "#fff" : "#612d91"} size={30} />
          </div>
        </div>

        {/* Form starts from here */}
        {showMail && (
          <div className="contact-form-details">
            <div className="contact-form-details-content">
              <div className="contact-form-details-content-box-one">
                <div className="contact-form-details-content-box-one-name">
                  <p>Full name</p>

                  <div className="contact-form-details-content-box-one-name-input">
                    <BsPersonCircle size={20} color="#612d91" />
                    <input
                      onChange={(e) => {
                        setName(e.target.value);
                      }}
                    />
                  </div>
                </div>

                <div className="contact-form-details-content-box-one-phone">
                  <p>Phone</p>

                  <div className="contact-form-details-content-box-one-phone-input">
                    <AiTwotonePhone size={20} color="#612d91" />
                    <input
                      type="email"
                      onChange={(e) => {
                        setContactNumber(e.target.value);
                      }}
                    />
                  </div>
                </div>

                <div className="contact-form-details-content-box-one-mail">
                  <p>Email</p>

                  <div className="contact-form-details-content-box-one-mail-input">
                    <BiMailSend size={20} color="#612d91" />
                    <input
                      type="email"
                      name="email_name"
                      onChange={(e) => {
                        setEmail(e.target.value);
                      }}
                    />
                  </div>
                </div>
              </div>

              <div className="contact-form-details-content-box-two">
                <p>Message</p>

                <textarea
                  name="message"
                  onChange={(e) => {
                    setMessage(e.target.value);
                  }}
                ></textarea>
              </div>
            </div>

            <div className="services-heading">
              <p>Services</p>
            </div>

            <div className="services-category-options">
              <div
                ref={web}
                className="services-category-options-one"
                onClick={() => {
                  setServiceOption("Web development");
                  web.current.style.backgroundColor = "#612d91"
                  web.current.style.color = "#fff"
                }}
              >
                Web development
              </div>

              <div
                className="services-category-options-two"
                ref={socialMedia}
                onClick={() => {
                  setServiceOption("Social media marketing");
                  web.current.style.backgroundColor = "#f9f9fb"
                  web.current.style.color = "#000"
                  socialMedia.current.style.backgroundColor = "#612d91"
                  socialMedia.current.style.color = "#fff"
                }}
              >
                Social media marketing
              </div>

              <div
                className="services-category-options-three"
                ref={graphicDesign}
                onClick={() => {
                  setServiceOption("Graphic design");
                  web.current.style.backgroundColor = "#f9f9fb"
                  web.current.style.color = "#000"
                  socialMedia.current.style.backgroundColor = "#f9f9fb"
                  socialMedia.current.style.color = "#000"
                  graphicDesign.current.style.backgroundColor = "#612d91"
                  graphicDesign.current.style.color = "#fff"
                }}
              >
                Graphic design
              </div>

              <div
                className="services-category-options-four"
                ref={video}
                onClick={() => {
                  setServiceOption("Video animation");
                  web.current.style.backgroundColor = "#f9f9fb"
                  web.current.style.color = "#000"
                  socialMedia.current.style.backgroundColor = "#f9f9fb"
                  socialMedia.current.style.color = "#000"
                  graphicDesign.current.style.backgroundColor = "#f9f9fb"
                  graphicDesign.current.style.color = "#000"
                  video.current.style.backgroundColor = "#f9f9fb"
                  video.current.style.color = "#000"
                }}
              >
                Video animation
              </div>
            </div>

            <div className="send-message-button-div">
              <button className="send-message-button" onClick={sendEmail}>
                Send message
              </button>
            </div>
          </div>
        )}

        {showMap && (
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2915.07631918385!2d-87.97369152459471!3d43.06086239054084!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x88051b9f9690602d%3A0xd8b2a5c875cd3a3b!2s4534%20W%20North%20Ave%2C%20Milwaukee%2C%20WI%2053208%2C%20USA!5e0!3m2!1sen!2s!4v1689334617767!5m2!1sen!2s"
            title="Google Map"
            height={450}
            style={{ border: 0, borderRadius: "5px", width: "70%" }}
            allowFullScreen=""
            loading="lazy"
            referrerPolicy="no-referrer-when-downgrade"
          />
        )}

        {showPhone && (
          <div className="show-contact-number-button-container">
            <div className="show-contact-number-button-container-button">
              <GiRotaryPhone size={25} color="#fff" />
              <div>+1 (917) 916-6473</div>
            </div>
          </div>
        )}
      </div>

      <form
        style={{ height: 0, overflow: "hidden" }}
        ref={form}
        onSubmit={sendEmail}
      >
        <label>Name</label>
        <input type="text" name="from_name" value={name} />
        <label>Email</label>
        <input type="email" name="email_name" value={email} />
        <label>Contact Number</label>
        <input type="email" name="contact_number" value={contactNumber} />
        <label>Message</label>
        <textarea name="message" value={message} />
        <input type="submit" value="Send" />
      </form>
    </>
  );
}
